import React, { useEffect } from "react";
import * as S from "./indexStyle";
import { useNavigate } from "react-router-dom";
import { useGetMe } from "../../../../store/getMe";

const MyProfile = () => {
  const { user, fetchUser } = useGetMe();
  const navigate = useNavigate();
  const logout = () => {
    localStorage.removeItem("ACCESS_TOKEN");
    localStorage.removeItem("REFRESH_TOKEN");
    alert(" .");
    navigate("/login");
    window.location.reload();
  };

  useEffect(() => {
    fetchUser();
  }, [fetchUser]);

  return user !== "" ? (
    <S.Container>
      <S.ProfileWrapper>
        <S.ProfileImg src="/images/profile.svg" />
        <S.ProfileDetail>
          <S.Title>{user.name}</S.Title>
          <S.Email>{user.email}</S.Email>
        </S.ProfileDetail>
      </S.ProfileWrapper>
      <S.Bottom>
        <S.BottomOption
          onClick={() => navigate("/bookmark")}
          style={{ borderBottomLeftRadius: "9px" }}
        >
          {user.role === "ROLE_ADMIN" ? " " : ""}
        </S.BottomOption>
        <S.BottomOption
          onClick={() => logout()}
          style={{ color: "rgb(176, 30, 30)", borderBottomRightRadius: "9px" }}
        >
          
        </S.BottomOption>
      </S.Bottom>
    </S.Container>
  ) : (
    <S.Container user={user}>
      <span
        style={{ color: "#2B32B2", cursor: "pointer" }}
        onClick={() => navigate("/login")}
      >
        
      </span>
    </S.Container>
  );
};

export default MyProfile;
