import React, { useState } from "react";
import * as S from "./indexStyle";
import { useNavigate } from "react-router-dom";
import axios from "axios";

const LoginContent = () => {
  const navigate = useNavigate();
  const [emailValue, setEmail] = useState("");
  const [pwValue, setPw] = useState("");

  const loginData = {
    email: emailValue,
    password: pwValue,
  };

  const login = async () => {
    if (isButtonActive) {
      try {
        const res = await axios.post("http://3.37.189.59/auth", loginData);
        if (res && res.data) {
          localStorage.setItem("ACCESS_TOKEN", res.data.data.accessToken);
          localStorage.setItem("REFRESH_TOKEN", res.data.data.refreshToken);
          navigate("/main");
          window.location.reload();
        }
      } catch (err) {
        if (err.response.data.status === 401) {
          alert("  .");
        } else {
          alert(" ");
        }
      }
    }
  };

  const activeEnter = (e) => {
    if (e.key === "Enter") {
      login();
    }
  };

  const isButtonActive = emailValue.length > 0 && pwValue.length > 0;

  return (
    <div>
      <S.Logo src="/images/logo.svg" onClick={() => navigate("/main")} />
      <S.IdContainer>
        <S.LoginInput
          type="text"
          placeholder=" "
          onChange={(e) => setEmail(e.target.value)}
          onKeyDown={(e) => activeEnter(e)}
        />
        <S.LoginInput
          type="password"
          placeholder=" "
          onChange={(e) => setPw(e.target.value)}
          onKeyDown={(e) => activeEnter(e)}
        />
        <div
          style={{
            display: "flex",
            justifyContent: "space-between",
            width: "385px",
          }}
        >
          <S.LoginOption>
            <S.Span style={{ color: "#B01E1E" }}>
                .
            </S.Span>
          </S.LoginOption>
        </div>
      </S.IdContainer>
      <S.Button
        style={{
          backgroundColor: isButtonActive ? "#242B9C" : "#CDCDCD",
          cursor: isButtonActive ? "pointer" : "",
        }}
        onClick={() => login()}
      >
        
      </S.Button>
      <div style={{ float: "right", marginTop: "10px" }}>
         ?{" "}
        <span
          style={{ color: "#242B9C", cursor: "pointer" }}
          onClick={() => navigate("/register")}
        >
          
        </span>
      </div>
    </div>
  );
};

export default LoginContent;
